import React, { useEffect, useRef } from 'react';
import { generateUrl } from '../utils/dataService';

const highlightText = (text, query) => {
  const parts = text.split(new RegExp(`(${query})`, 'gi'));
  return parts.map((part, index) =>
    part.toLowerCase() === query.toLowerCase() ? <span key={index} className="highlight">{part}</span> : part
  );
};

const TypeaheadList = ({ suggestions, query, selectedIndex, onSelect }) => {
  const listRef = useRef(null);

  useEffect(() => {
    if (selectedIndex >= 0 && listRef.current) {
      const selectedItem = listRef.current.children[selectedIndex];
      if (selectedItem) {
        selectedItem.scrollIntoView({
          behavior: 'smooth',
          block: 'nearest'
        });
      }
    }
  }, [selectedIndex]);

  const identifierContent = (suggestion) => {
    const isIdentifierMatch = suggestion.identifier.toLowerCase() === query.toLowerCase();
    return isIdentifierMatch ? <span className="highlight">{suggestion.identifier}</span> : suggestion.identifier;
  };

  return (
    <ul className="typeahead-list" id="typeahead-list" role="listbox" ref={listRef}>
      {suggestions.map((suggestion, index) => (
        <li key={suggestion.id}>
          <a
            id={`suggestion-${index}`}
            role="option"
            aria-selected={index === selectedIndex}
            className={`typeahead-item ${index === selectedIndex ? 'selected' : ''}`}
            data-resource={suggestion.resourced ? 'true' : 'false'}
            title={suggestion.resourced ? `${suggestion.identifier} resources available` : ''}
            href={generateUrl(`/languages/${suggestion.identifier}/`)}
            onClick={() => onSelect(suggestion)}
          >
            <span className="identifier">{identifierContent(suggestion)}</span>
            <span className="label">{highlightText(suggestion.label, query)}</span>
          </a>
        </li>
      ))}
    </ul>
  );
};

export default TypeaheadList;
